.slider-content{
    position: fixed;
    overflow: hidden;
    z-index: 1;
    width: 100%;
    bottom: 0;
    font-size: 22px;
    box-shadow: 20px 20px 50px 15px grey;
    background-color: rgb(20, 22, 22);
    animation-name: slide-left;
    animation-duration: 2.2s;
    &-wrapper{
        padding: 15px;
        color: rgb(197, 186, 186);
        display: flex;
        justify-content: space-between;
        .data{
            .slider-content-name , .slider-content-price{
                display: flex;
                padding: 5px;
                font-weight: 400;
                
                .value{
                    font-weight: bold;
                    padding: 0px 5px;
                    @media (max-width:768px) {
                        padding: 0px;
                    }
                }
                    
                @media (max-width:768px) {
                    flex-direction: column;
                }
            }
        }
        .arrow-container{
            margin: 20px;
            width: 50px;
            height: auto;
            border-radius: 10px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            
            @media (max-width:768px) {
                margin: 0;
            }
        }
        .close{
            font-size: 14px;
            color: red;
            margin-right: 10px;
            margin-top: 5px;
        }
    }

    @media (max-width:768px) {
        font-size: 16px;
    }
}